/* Social hint dimensions */
.image {
  display: block;
  max-width: 100%;
  height: auto; }

.image--centered {
  margin-left: auto;
  margin-right: auto; }

.image--avatar {
  margin-top: 1rem;
  margin-bottom: 1rem; }

.image--circular {
  border-radius: 50%; }

.image--avatar-description {
  max-width: 400px;
  width: 90%;
  margin: 1rem auto;
  font-size: 18px; }

.image--header-avatar {
  display: block;
  max-width: 100%;
  height: auto;
  margin-top: 1.6rem;
  margin-bottom: -16%; }

@media (min-width: 992px) {
  .image--header-avatar {
    margin-bottom: -36%; } }
